.item {
    width: 350px;
    /* border: 2px solid red; */
}
/* .item img {
    width: 100%;
} */
.item p {
    margin: 6px 0;
    font-size: 0.9rem;
}
.item-prices{
    display: flex;
    gap: 20px;
}
.item-price-new {
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}
.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover {
    transform: scale(1.05);
    transform: 0.6sec;
}